import React from "react";

const columnStyle = {
    borderTopStyle: "solid", 
    borderTopWidth: "2", 
    borderTopColor: "white",
}
const lColumnWidth = 25;
const gapWidth = 5;

export function Subsection(props)
{
    return (
        <li style={{display: "flex", flexDirection: "row", justifyContent: "space-between"}}>
            <div style={{...columnStyle, width: `${lColumnWidth}%`, marginRight: `${gapWidth}`}}>
                <div>{props.title}</div>
                <div>{props.company}</div>
                <div>{props.location}</div>
            </div>
            <div style={{...columnStyle, width: `${100 - lColumnWidth - gapWidth}%`}}>
                <div style={{textAlign: "end"}}>{props.date}</div>
                <ul>
                    {props.description.map((item, i) => {
                        return <li key={i}>{item}</li>
                    })}
                </ul>
            </div>
        </li>
    )
}

export function SmallSubsection(props)
{
    return (
        <li style={{display: "flex", flexDirection: "row", justifyContent: "space-between"}}>
            <div style={{...columnStyle, width: `${lColumnWidth}%`, marginRight: `${gapWidth}`}}>
                <div>{props.title}</div>
                <div>{props.company}</div>
            </div>
            <div style={{...columnStyle, width: `${100 - lColumnWidth - gapWidth}%`}}>
                <div style={{textAlign: "end"}}>{props.date}</div>
                <div style={{textAlign: "end", paddingBottom: "5%"}}>{props.description}</div>
            </div>
        </li>
    )
}